import * as types from '../actions/collectionsConsts';
import {getInitialCollectionState} from '../actions/collectionsConsts';
import {arrayMove} from '../Helpers';

export default function reducer(state=getInitialCollectionState(), action){
  const {payload} = action;

  switch(action.type){
    case types.LOAD_COLLECTIONS:{
      return payload.collections;
    }
    case types.LOAD_ADD_COLLECTIONS:{
      return Object.assign({}, state, payload.collections);
    }
    case types.UNLOAD_COLLECTIONS:{
      return {};
    }
    case types.ADD_COLLECTION:{
      const {collection} = payload;
      return{...state,[collection.id]:collection}
    }
    case types.REMOVE_COLLECTION:{
      const {collectionId} = payload;
      const collections = Object.assign({}, state);
      delete collections[collectionId];
      return collections;
    }
    case types.MOVE_COLLECTION:{
      const {collectionId} = payload;
      let newState = Object.assign({},state);
      delete newState[collectionId];
      return newState;
    }
    case types.ADD_EFFECT_TO_COLLECTION:{
      const {collectionId,effectId} = payload;
      const effects = state[collectionId].effects.concat(effectId);
      return updateState(collectionId,'effects',effects);
    }
    case types.MOVE_EFFECT:{
      const {collectionId,oldIndex,newIndex} = payload;
      const effects = state[collectionId].effects.slice();
      return updateState(collectionId,'effects',arrayMove(effects,oldIndex,newIndex));
    }
    case types.REMOVE_EFFECT_FROM_COLLECTION:{
      const {collectionId,effectId} = payload;
      if(typeof state[collectionId] === "undefined"){return state;}
      const effects = state[collectionId].effects.slice();
      for(let i = effects.length - 1; i >= 0; i--){
        if(effects[i] === effectId){
          effects.splice(i,1);
        }
      }
      return updateState(collectionId,'effects',effects);
    }
    case types.REMOVE_EFFECT_REFERENCE:{
      const {collectionId, effectId} = payload;
      const effects = state[collectionId].effects.filter(e => e !== effectId);
      return updateState(collectionId, "effects", effects);
    }
    case types.ADD_EFFECT_REFERENCE:{
      const {collectionId, effectId, index} = payload;
      const effects = state[collectionId].effects.slice();
      effects.splice(index,0,effectId);
      return updateState(collectionId,"effects",effects);
    }
    case types.CHANGE_COLLECTION_VAR:{
      const {id, varName, value} = payload;
      return updateState(id,varName,value);
    }
    default: {return state}
  }

  function updateState(collectionId,varName,value, _state = null){
    if(_state !== null){
      return{
        ..._state,
        [collectionId]:Object.assign({},_state[collectionId],{[varName]:value})
      }
    }
    return{
      ...state,
      [collectionId]:Object.assign({},state[collectionId],{[varName]:value})
    }
  }
}



