import React,{useRef} from 'react';
import {useDrag} from 'react-dnd';
import {DragTypes} from '../../constants/constants';

const LibraryDraggable = ({fileName, filePath, classes, children}) =>{
  const dragRef = useRef(null);
  const[{isDragging}, drag, preview] = useDrag({
    item:{type:DragTypes.LIBRARYSOUND, id:filePath, filePath, fileName},
    collect:monitor=>({
      isDragging: monitor.isDragging()
    })
  });
  preview(drag(dragRef));
  return(
    <div className={classes} ref={dragRef}>
      {children}
    </div>
  );
}

export default LibraryDraggable;



