import React from 'react';
import {useDrop} from 'react-dnd';
import {DragTypes} from '../../constants/constants';

const LibraryDropTarget = ({onDrop, children}) =>{
  const [{canDrop},drop] = useDrop({
    accept: DragTypes.LIBRARYSOUND,
    drop(item){
      onDrop(item.type,item.fileName,item.filePath);
    },
    collect: monitor =>({
      canDrop: monitor.canDrop()
    })
  });
  return(
    <div ref={drop} className={canDrop ? "droppable" : ""}>
      <div className="droppable-overlay">
        <p>Drop Here</p>
      </div>
      {children}
    </div>
  )
}
export default LibraryDropTarget;



