import React, {useRef} from 'react';
import {useDrag, useDrop} from 'react-dnd';

import {isUndefined} from '../../Helpers';

const SortableDragAndDrop = ({id,parentId,i,type,callbacks,children, classes, vertical, dragRef, noSiblings}) =>{
  const dropRef = useRef(null);
  if(isUndefined(vertical)){vertical = true;}
  const [,drop] = useDrop({
    accept: type,
    hover(item, monitor){
      if(id === item.id){return;}
      if(parentId === item.parentId){
        //console.log(`Hover In Parent ${parentId}`);
        if(!isUndefined(noSiblings)){return;}
        if(i === item.i){return;}
        if(isPointerWithinRange(item.i, i, monitor, dropRef, vertical)){
          callbacks.onHoverSibling(parentId, id, item.i,i);
          item.i = i;
        }
      }
      else{
        callbacks.onHoverNonSibling(item.id,i,item.parentId);
        item.parentId = parentId;
      }
    },
    drop(){

    }
  });
  const [{isDragging}, drag, preview] = useDrag({
    item: {id, parentId, type, i},
    collect: monitor =>({
      isDragging: monitor.isDragging()
    })
  });
  const opacity = isDragging ? 0:1;
  if(!isUndefined(dragRef)){
    drag(dragRef);
  }
  preview(drop(dropRef));
  return(
    <div ref={dropRef} style={{opacity}} className={isUndefined(classes) ? "" : classes}>
      {children}
    </div>
  )
}
const isPointerWithinRange = (dragI, hoverI, monitor, ref, vertical = true) =>{
  const hoverBRect = ref.current.getBoundingClientRect();
  const clientOffset = monitor.getClientOffset();

  if(vertical){
    const hoverMidY = (hoverBRect.bottom - hoverBRect.top) / 2;
    const hoverClientY = clientOffset.y - hoverBRect.top;

    if(dragI < hoverI && hoverClientY < hoverMidY){return false;}
    if(dragI > hoverI && hoverClientY > hoverMidY){return false;}

    return true;
  }
  else{
    const hoverMidX = (hoverBRect.right - hoverBRect.left) / 2;
    const hoverClientX = clientOffset.x - hoverBRect.left;

    if(dragI < hoverI && hoverClientX < hoverMidX){return false;}
    if(dragI > hoverI && hoverClientX > hoverMidX){return false;}

    return true;
  }
}

export default SortableDragAndDrop;



